import React, { Fragment } from "react";
import react from "../../styles/assets/images/FramworksHD/React.png";
import node from "../../styles/assets/images/FramworksHD/node-js.png";
import angular from "../../styles/assets/images/FramworksHD/Angular.png";
import js from "../../styles/assets/images/FramworksHD/js.png";
import mongodb from "../../styles/assets/images/FramworksHD/mongodb.avif";
import solidity from "../../styles/assets/images/FramworksHD/solidity.avif";

const StateOfArt = () => {

  return (
    <section class="mern-section">
    <div class="container grid grid--2-cols">
      <div class="firstgrid">
        <h4 class="heading-fourth">State-of-the-art</h4>
        <h3 class="heading-tertiary">
          Technology Stack Our <em>Smart Contract Developers</em> Use
        </h3>
        <p class="description">
          Justtry has a team of passionate and expert developers who are
          well versed in the latest web technologies and frameworks.
          Whatever technology or framework you name it our developer will
          develop a perfect web solution using the desired framework to
          ensure your business success.
        </p>
      </div>

      <div class="container">
        <div class="container grid grid--3-cols">
          <figure class="gallery-item">
            <img
              class="techstack-img"
              src={react}
              alt="Techstack Logos"
            />
            <figcaption>React</figcaption>
          </figure>
          <figure class="gallery-item">
            <img
              class="techstack-img"
              src={angular}
              alt="Techstack Logos"
            />
            <figcaption>Angular</figcaption>
          </figure>
          <figure class="gallery-item">
            <img
              class="techstack-img"
              src={node}
              alt="Techstack Logos"
            />
            <figcaption>Node</figcaption>
          </figure>
          <figure class="gallery-item">
            <img
              class="techstack-img"
              src={mongodb}
              alt="Techstack Logos"
            />
            <figcaption>Mongo</figcaption>
          </figure>
          <figure class="gallery-item">
            <img
              class="techstack-img"
              src={js}
              alt="Techstack Logos"
            />
            <figcaption>React</figcaption>
          </figure>

          <figure class="gallery-item">
            <img
              class="techstack-img"
              src={solidity}
              alt="Techstack Logos"
            />
            <figcaption>Solidity</figcaption>
          </figure>
        </div>
      </div>
    </div>
  </section>
  );
};

export default StateOfArt;
